import * as React from "react";
import Helmet from "react-helmet";
//
import { Footer } from "../components/Footer";
import { Header } from "../components/Header";
import { PageIntro } from "../components/PageIntro";
import { BottomBanner } from "../components/BottomBanner";
import { useStaticQuery, graphql } from "gatsby";
import { LayoutContainer } from "../components/LayoutContainer";
import { HoverCard } from "../components/HoverCard";
import { GRADIENT_1 } from "../styles";
import { cl } from "../helpers/general";
import { CareerModal } from "../components/CareerModal";
import { SectionDescription } from "../components/SectionDescription";
//

const Careers = () => {
  const rawData = useStaticQuery(graphql`
    query {
      allSanityCareers {
        nodes {
          title
          subtitle
          highlight
          description
          isOpen
          requirements
          benefits
          type
        }
      }
    }
  `);

  const [selectedJob, setSelectedJob] = React.useState<any>();

  const data: any[] = rawData.allSanityCareers.nodes;

  const sections = [
    {
      title: "Volunteers",
      data: data.filter((item) => item.type == "volunteer"),
    },
    {
      title: "Other Jobs",
      data: data.filter((item) => item.type == "otherJob"),
    },
  ].filter(item => item.data.length);

  return (
    <>
      <Header hasPageIntro={true} />
      <PageIntro
        title="Careers"
        subTitle="Looking for an opportunity to work with Quizzario? Apply now."
      />
      <LayoutContainer className="sm:py-24">
        {sections.map((item) => {
          return (
            <>
              <SectionDescription
                bigTitle={item.title}
                smallTitle="Quizzario"
              />
              <div className="sm:grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8 flex flex-col">
                {item.data
                  .sort((a, b) => b.isOpen - a.isOpen)
                  .map((item, index) => {
                    return (
                      <HoverCard
                        key={index}
                        className={cl(
                          !item.isOpen && "opacity-50 pointer-events-none"
                        )}
                      >
                        {item.highlight && (
                          <div className="absolute right-0 bottom-0 m-5 rounded-md px-2 py-1 text-xs bg-brand-nine text-brand-four font-bold">
                            {item.highlight.toUpperCase()}
                          </div>
                        )}
                        <div className="text-xl mb-1">{item.title}</div>
                        <div className="opacity-50 mb-4">{item.subtitle}</div>
                        <span
                          onClick={() => setSelectedJob(item)!}
                          className={cl(
                            "text-transparent bg-clip-text cursor-pointer",
                            GRADIENT_1
                          )}
                        >
                          {item.isOpen ? "See Details" : "Closed"}
                        </span>
                      </HoverCard>
                    );
                  })}
              </div>
            </>
          );
        })}
      </LayoutContainer>
      {selectedJob && (
        <CareerModal onClose={() => setSelectedJob(null)!} data={selectedJob} />
      )}
      <BottomBanner />
      <Footer />
    </>
  );
};

export default Careers;
