import React from "react";
import { PropsWithChildren } from "react";
import { cl } from "../helpers/general";

interface Props extends PropsWithChildren {
  stayHighlighted?: boolean;
  colors?: string[];
  className?: string;
}

export const HoverCard = (props: Props) => {
  const colors = props.colors || ["from-brand-seven", "to-brand-eight"];

  return (
    <div
      className={cl(
        "relative rounded-2xl overflow-hidden group p-6 bg-brand-four z-20 text-center sm:text-left flex-shrink-0 w-full sm:w-auto",
        props.className
      )}
    >
      <div
        className={cl(
          "bg-gradient-to-br absolute inset-0 transition-opacity group-hover:opacity-100 -z-10 rounded-2xl",
          ...colors,
          props.stayHighlighted ? "" : "sm:opacity-0"
        )}
      >
        <div className="bg-black absolute inset-[1px] rounded-2xl"></div>
      </div>
      <span
        className={cl(
          "bg-gradient-to-br absolute inset-[1px] opacity-30  transition-opacity group-hover:opacity-30 -z-10 rounded-2xl",
          ...colors,
          props.stayHighlighted ? "" : "sm:opacity-0"
        )}
      />
      {props.children}
    </div>
  );
};
